@import url('https://fonts.googleapis.com/css2?family=MuseoModerno:wght@500&display=swap');

button {
  border: 1.5px solid;
  border-radius: 10px;
  padding: 10px 20px;
  margin: 10px 0;
  background-color: transparent;
  font-family: 'MuseoModerno', cursive;
  font-size: 1.25rem;
  font-weight: bold;
  cursor: pointer;
}

button:hover,
button:focus {
  outline: none;
}
